<template>
    <div class="loader">
        <div class="preloader">
            
            <div class="spinner">
                <div class="rect1"></div>
                <div class="rect2"></div>
                <div class="rect3"></div>
                <div class="rect4"></div>
                <div class="rect5"></div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            per:0,
        }
    }, 
   computed : {
       percen(){
           return this.per.toFixed()
       }
   },
    created() {
        let intval = setInterval(() => {
            if(this.per<100){
            this.per +=.1;
            }else{
                clearInterval(intval)
            }
        }, 10);
    },
}
</script>
<style>
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
    
    .spinner {
        /* margin: 100px 10px; */
        width: 80px;
        height: 50px;
        text-align: center;
        font-size: 10px;

    }

    .spinner>div {
        background-color: #479ee4;
        width: 4px;
        height: 35px;
        border-radius: 2px;
        margin: 4px;
        -webkit-animation-fill-mode: both;
        animation-fill-mode: both;
        display: inline-block;
        -webkit-animation: line-scale-pulse-out 0.9s -0.6s infinite cubic-bezier(0.85, 0.25, 0.37, 0.85);
        animation: line-scale-pulse-out 0.9s -0.6s infinite cubic-bezier(0.85, 0.25, 0.37, 0.85);


    }


    .spinner .rect4,
    .spinner .rect2 {
        -webkit-animation-delay: -0.4s;
        animation-delay: -0.4s;
    }

    .spinner .rect5,
    .spinner .rect1 {
        -webkit-animation-delay: -0.2s;
        animation-delay: -0.2s;
    }

    /* ################## loading bar ################ */
 
    

   
    @-webkit-keyframes line-scale-pulse-out {
        0% {
            -webkit-transform: scaley(1);
            transform: scaley(1);
        }

        50% {
            -webkit-transform: scaley(0.4);
            transform: scaley(0.4);
        }

        100% {
            -webkit-transform: scaley(1);
            transform: scaley(1);
        }
    }

    @keyframes line-scale-pulse-out {
        0% {
            -webkit-transform: scaley(1);
            transform: scaley(1);
        }

        50% {
            -webkit-transform: scaley(0.4);
            transform: scaley(0.4);
        }

        100% {
            -webkit-transform: scaley(1);
            transform: scaley(1);
        }
    }
</style>