<template>

  <div class="home">
      <div v-if="load" class="loader__page">

            <div class="loading-bar">
                <div class="percentage" :style="{'width':per+'%'}"></div>
            </div>
    <loaderpage/>
</div>
      <div v-else>
    <div class="btn_content">

      <div class="btn_item" @click="show">
        <span :class="bool ?'active ':'line'"></span>
        <span :class="bool ?'active1 ':'line1'"></span>
        <span :class="bool ?'active2 ':'line2'"></span>
      </div>
      <h3>{{bool ?'Close' :'More INfo'}}</h3>

    </div>



    <!-- homepage content -->
    <div :class="bool?'homeAni':'home-page'">

      <particles-bg type="cobweb" color="#757575" :bg="true"
        style="width: 100%;height: 100%; z-index: -1;opacity:0.35;line-height: 1.765;position: absolute; " />
    
      <div class="gradient-overlay"></div>
      <pageHome/>
    </div>
    <!-- end of homepage content -->
    <!-- sidebar content -->
    <div :class="bool?'sideAni':'sidebar'">

      <sidebar />

    </div>
    <!-- end of sidebar content -->
</div>


    <!-- end of btn content -->
    <!-- ######### Home Page ################## -->


    <!-- ############### Sidebar Page ############3 -->

    <!-- side -->


  </div>
</template>
<script>
  // @ is an alias to /src
  import {
    ParticlesBg
  } from "particles-bg-vue";
  import sidebar from '../components/sidebar'
  import loaderpage from '../components/loaderpage'
  import pageHome from '../components/pageHome'

  export default {
    name: 'Home',
    components: {
      ParticlesBg,
      sidebar,
      loaderpage,
      pageHome
  

    },

    data: () => ({
      bool: false,
load:true,
per:0,
    }),

    methods: {
      show() {
        this.bool = !this.bool
      },
    
      },

      created() {
        let num = Math.floor(Math.random())+1;
        
        let intval = setInterval(() => {
            if(this.per < 100){
          
            this.per +=num;
            }
            else if (this.per >= 100){
               
                this.load = false;
                clearInterval(intval)
            }
        }, 10);
    },
    

  }
</script>
<style >
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
.home{
    overflow-x: hidden;
    overflow: hidden;
   overflow-y: hidden;
 
}
 


  /* ############ btn content ############## */
  .btn_content {
    padding: 26px 40px;
    display: flex;
    z-index: 11;
    position: absolute;
    top: 5px;
    justify-content: center;
    align-items: center;

  }

  .btn_content h3 {
    font-size: 1rem;
    font-weight: 200;
    padding-left: 10px;
    padding-top: 10px;
    text-transform:uppercase;
    text-align: center;
    align-self: baseline;
    color: white;
  }

  .btn_item {
    width: 40px;
    height: 40px;
    padding: 5px 8px;
    border: 1px solid #111111;
    background-color: #000000;
    transition: all 1s ease;
  }

  .line,
  .line1,
  .line2 {
    margin-top: 6px;

    width: 22px;
    height: 1.4px;
    background-color: #479EE4;
    display: block;
    transition: all 0.5s ease;
  }

  .active {
    transform: rotate(50deg) translate(-3px, 10px);
    width: 22px;
    height: 1.1px;
    margin: 8px 10px;
    background-color: #479EE4;
    display: block;
    transition: all 1s ease;
  }

  .active1 {
    opacity: 0;

  }

  .active2 {
    transform: rotate(-50deg) translate(-3px, -11px);
     width: 22px;
    height: 1.1px;
    margin: 8px 10px;
    background-color: #479EE4;
    display: block;
    transition: all 1s ease;
  }

  /* ########################## */
  .home-page {
    min-height: 100vh;
    width: 100%;
    position: relative;
    left: 0;
    top: 0;
  overflow: hidden;
    background: #141414;
    background: radial-gradient(circle, #141414 0%, black 100%);
   z-index: 10;
    transition: all 0.7s ease;
    overflow-x: hidden;
  
  }

  .sidebar {
    min-height: 100vh;
    width: 100%;
    position: fixed;
    left: -50%;
    top: 0;

    background-color: #111111;
    visibility: hidden;
    transition: all 1.2s ease;
    overflow-x: hidden;

  }

  .homeAni {
    min-height: 100vh;
    width: 100%;
    position: relative;
    left: 100%;
    top: 0;
    background: #141414;
    background: radial-gradient(circle, #141414 0%, black 100%);
    z-index: 9;
    transition: all 0.9s ease;
    overflow-x: hidden;

  }

  .gradient-overlay {
    display: block;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: .6;
    background: linear-gradient(to right, black 0%, black 20%, transparent 100%);
  }

  .sideAni {

    min-height: 100vh;
    overflow-x: hidden;

    width: 100%;
    position: absolute;
    left: 0;
    top: 0;

    background-color: #111111;
visibility: visibles;
    transition: all 0.6s ease;
  }
  
     .loader__page{
         display: flex;
        justify-content: center;
        align-items: center;
       position: absolute;
       top: 0;
       left: 0;
        width: 100%;
        height: 100vh;
        z-index: 100;
        overflow-x: hidden;
        
        background: #111111;
  }
   .loading-bar {
        position: absolute;
        width: 100%;
        height: 4px;
  top: 0;
        overflow: hidden;
        
        
    }
        .percentage {
          
         
            display: block;
            height: 100%;
      
            border-radius: 15px;
            background-color: #479ee4;
            background-size: 30px 30px;
        }

</style>